import React                                        from 'react';
import PropTypes                                    from 'prop-types';





import {
    Grid , Row , Col
}                                                   from 'react-bootstrap';
import FlatButton                                   from 'material-ui/FlatButton';
import TextField                                    from 'material-ui/TextField';
import {
    RadioButton
    , RadioButtonGroup
}                                                   from 'material-ui/RadioButton';
import RaisedButton                                 from 'material-ui/RaisedButton';








/* ################################################### */
/*          abstract         */
/* ################################################### */
import ComponentAbstract                            from '../../../../_abstract/c.abstract';






import './s.docUpload.step.3.css';





export default class ComponentDocUploadStep3 extends ComponentAbstract {


    static propTypes = {
        form                : PropTypes.object

        , data              : PropTypes.array

        , result            : PropTypes.object

        , onClickPDF        : PropTypes.func
        , onClickPrint      : PropTypes.func
        , onClickOK         : PropTypes.func
    }


    static defaultProps = {
        form                : {}

        , data              : []

        , result            : {}

        , onClickPDF        : () => {}
        , onClickPrint      : () => {}
        , onClickOK         : () => {}
    }










    constructor( props ) {
        super( props )
    }









    /*
        ################################################################################################################################################
        ################################################################################################################################################
                render
        ################################################################################################################################################
        ################################################################################################################################################
    */
    render() {

        return (
                <div>

                    <h5
                            style={ {
                                marginBottom    : 0
                                , marginTop     : 15
                                , marginBottom  : 30
                            } }>
                        Acknowledgement
                    </h5>

                    <hr />

                    <div className={ ' companyOnboard-upload-step3-margin-20 companyOnboard-upload-step3-border companyOnboard-upload-step3-padding-10 ' }>
                        Document is upload and success
                    </div>

                    <div className={ ' companyOnboard-upload-step3-padding ' }>

                        <Grid>
                            <Row className="show-grid">
                                <Col md={ 4 } >
                                    CIF Number
                                </Col>

                                <Col md={ 6 }>
                                    { this.props.form[ 'cifNumber' ] }
                                </Col>
                            </Row>
                        </Grid>

                    </div>


                    <div className={ ' companyOnboard-upload-step3-padding ' }>

                        <Grid>
                            <Row className="show-grid">
                                <Col md={ 4 } >
                                    Company Name
                                </Col>

                                <Col md={ 6 }>
                                    { this.props.form[ 'companyName' ] }
                                </Col>
                            </Row>
                        </Grid>

                    </div>


                    <div className={ ' companyOnboard-upload-step3-padding ' }>

                        <Grid>
                            <Row className="show-grid">
                                <Col md={ 4 } >
                                    Select Service Package
                                </Col>

                                <Col md={ 6 }>
                                    {
                                        ( () => {
                                            let label = '';
                                            for ( let i = 0; i < this.props.data.length; i ++ ) {
                                                let ObjJ = this.props.data[ i ];
                                                if ( ObjJ[ 'value' ] == this.props.form[ 'servicePackage' ] ) {
                                                    label = ObjJ[ 'label' ];
                                                }
                                            }
                                            return label;
                                        } )()
                                    }
                                </Col>
                            </Row>
                        </Grid>

                    </div>


                    <div className={ ' companyOnboard-upload-step3-textAlign-center ' }>

                        <RaisedButton
                            label={ 'Download PDF' }
                            className={ ' companyOnboard-upload-step3-margin-20 ' }
                            onClick={ () => {
                                this.props.onClickPDF();
                            } } />

                        <RaisedButton
                            label={ 'Print' }
                            className={ ' companyOnboard-upload-step3-margin-20 ' }
                            onClick={ () => {
                                this.props.onClickPrint();
                            } } />

                        <RaisedButton
                            label={ 'OK' }
                            className={ ' companyOnboard-upload-step3-margin-20 ' }
                            onClick={ () => {
                                this.props.onClickOK();
                            } } />

                    </div>



                </div>
        );
    };
}



