import React                                        from 'react';
import PropTypes                                    from 'prop-types';
import {
    connect
}                                                   from 'react-redux';
import axios, { post }                              from 'axios';




import {
  Grid , Row , Col
}                                                   from 'react-bootstrap';

import {
    Table,
    TableBody,
    TableHeader,
    TableHeaderColumn,
    TableRow,
    TableRowColumn,
  }                                                 from 'material-ui/Table';
import {
  Step,
  Stepper,
  StepLabel,
}                                                   from 'material-ui/Stepper';
import Paper                                        from 'material-ui/Paper';
import FlatButton                                   from 'material-ui/FlatButton';










/* ################################################### */
/*          redux         */
/* ################################################### */

import ReduxActionCompanyOnboardUpload , { ReduxEnumCompanyOnboardUpload } from '../../../../_redux/action/reduxAction.company.onboard.upload';






/* ################################################### */
/*          constant         */
/* ################################################### */

import { AppConstant }                              from '../../../../_constant/constant.app';
import { ServiceEnumHTTP }                          from '../../../../_service/helper/service.enum.http';
