import HelperNavigate , { HelperNavigateENUM }		from '../../../_helper/h.navigate';
import HelperStorage		                        from '../../../_helper/h.storage';
import HelperVerifyAuthorize                        from '../../../_helper/h.verifyAuthorize';

import { ObjectUtil }                               from '../../../../_util/util.object';
import { StringUtil }                               from '../../../../_util/util.string';

import FormValidateUtil                             from '../../../../_util/util.formValidate';




/* ################################################### */
/*          abstract         */
/* ################################################### */
import ComponentAbstract                            from '../../../_abstract/c.abstract';




/* ################################################### */
/*          component         */
/* ################################################### */

import ComponentCustomLoading                       from '../../../_component/loading/cc.loading';
import ComponentCustomAlert                         from '../../../_component/alert/cc.alert';


import ComponentDocUploadDocument                   from './document/c.docUpload.document';

import ComponentDocUploadStep1                      from './step1/c.docUpload.step.1';
import ComponentDocUploadStep2                      from './step2/c.docUpload.step.2';
import ComponentDocUploadStep3                      from './step3/c.docUpload.step.3';







import './s.onboardUpload.detail.css';









export default connect(
        ( state ) => {

            let thisState = state[ 'ReduxCompanyOnboardUpload' ] ;
            let stateObj = { };

            if ( thisState ) {

                for ( let i = 0 ; i < thisState.length; i ++ ) {
                    let recordItem = thisState[ i ];
                    if ( !recordItem[ 'completed' ] ) {


                        if ( ReduxEnumCompanyOnboardUpload.INIT_DONE == recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestInitDone';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                        } else if ( ReduxEnumCompanyOnboardUpload.SERVICE_PACKAGE_DONE == recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestServicePackageDone';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                        } else if ( ReduxEnumCompanyOnboardUpload.SUBMIT_DONE == recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestSubmitStep1Done';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                        } else if ( ReduxEnumCompanyOnboardUpload.PREVIEW_DONE == recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestSubmitStep2Done';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                        } else if ( ReduxEnumCompanyOnboardUpload.ACKNOWLEGDEMENT_DONE == recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestSubmitStep3Done';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                        } else if ( ReduxEnumCompanyOnboardUpload.DOWNLOAD_PRINT_DONE == recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestDownloadPrintDone';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                        } else if ( ReduxEnumCompanyOnboardUpload.DOWNLOAD_PDF_DONE == recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestDownloadPDFDone';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                        } else if ( ReduxEnumCompanyOnboardUpload.ERROR == recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestError';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                        }


                        recordItem[ 'completed' ] = true;
                    }
                }

            }

            return stateObj;

        }
    ) (

         class ComponentDocUploadUpload extends ComponentAbstract {



            constructor( props ) {
                super( props )

                this.state = {

                    step : 0

                    , isLoading : false
                    , isDisableUpload : false

                    , isError : false
                    , errorMessage : ''


                    , data : {

                        documentCategory : [
                            {
                                label : 'Onboard'
                                , value : 'onboard'
                            }
                        ]

                        , servicePackage : [ ]
                    }

                    , form : {

                        formDoc : {
                            thumbnailDocumentId         : ''
                            , thumbnailUploadDate       : ''
                            , thumbnailUploadBranch     : ''
                            , thumbnailUploadBy         : ''
                            , thumbnailUrl              : ''

                            , file                      : null
                            , fileCompletePercentage    : 0
                        }


                        , form1 : {
                            cifNumber                   : ''
                            , companyName               : ''
                            , servicePackage            : ''
                        }


                    }

                    , formValidation : {}

                    , result : {

                        thumbnails : [ ]

                        , acknowledgement : {}

                    }

                }
            }











            /*
                ################################################################################################################################################
                ################################################################################################################################################
                        listener
                ################################################################################################################################################
                ################################################################################################################################################
            */

            _onReady() {
                this.requestServicePackage();
            }









            /*
                ################################################################################################################################################
                ################################################################################################################################################
                        event
                ################################################################################################################################################
                ################################################################################################################################################
            */

            onClickUpload( ) {
                console.log( 'ComponentDocUploadUpload : onClickUpload : *** : file => ' , this.state.form.formDoc.file );


                this.setState(
                    {
                        step : 1
                        , isDisableUpload : true
                    }
                    , () => {

                        HelperVerifyAuthorize.verify()
                            .then(
                                () => {

                                    Promise.all( [
                                        ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                                        , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                                    ] ).then(
                                        ( value ) => {

                                            if ( AppConstant.isDebug() ) {

                                                let form = this.state.form;
                                                let result = this.state.result;

                                                form[ 'formDoc' ][ 'file' ] = null;
                                                result[ 'thumbnails' ].push( {
                                                    documentId : ''
                                                    , links : [
                                                        {
                                                            rel : ''
                                                            , href : ''
                                                            , method : ''
                                                        }
                                                    ]
                                                } );

                                                this.setState( {
                                                    isDisableUpload     : false
                                                    , form              : form
                                                    , result            : result
                                                } );

                                                return;
                                            }

                                            let formDataTmp = new FormData();
                                            formDataTmp.append( 'file' , this.state.form.formDoc.file );


                                            // axios.post(
                                            //     AppConstant.HTTP_SERVER_IP + ServiceEnumHTTP.API.DOCUMENT_ONBOARD_UPLOAD
                                            //     , formDataTmp
                                            //     , {
                                            //         onUploadProgress: ( progressEvent ) => {
                                            //             let form = this.state.form;
                                            //             form[ 'formDoc' ][ 'fileCompletePercentage' ] = ( progressEvent.loaded / progressEvent.total ) * 100;
                                            //             this.setState( { form : form } );
                                            //         }
                                            //     }
                                            // )

                                            axios( {
                                                    method      : 'POST'
                                                    , url       : AppConstant.HTTP_SERVER_IP + ServiceEnumHTTP.API.DOCUMENT_ONBOARD_UPLOAD
                                                    , data      : formDataTmp
                                                    , headers     : {
                                                        'Authorization' : value[ 0 ] + ' ' + value[ 1 ]
                                                        , 'Content-Type': 'multipart/form-data'
                                                    }
                                                    , onUploadProgress: ( progressEvent ) => {
                                                        let form = this.state.form;
                                                        form[ 'formDoc' ][ 'fileCompletePercentage' ] = ( progressEvent.loaded / progressEvent.total ) * 100;
                                                        this.setState( { form : form } );
                                                    }
                                            } ).then(
                                                ( responsePost ) => {

                                                    axios( {
                                                        method      : 'GET'
                                                        , url       : AppConstant.HTTP_SERVER_IP + StringUtil.replace( ServiceEnumHTTP.API.DOCUMENT_ONBOARD_DOWNLOAD , '{documentId}' , responsePost.data.documentId )
                                                        , headers     : {
                                                            'Authorization' : value[ 0 ] + ' ' + value[ 1 ]
                                                            , 'Content-Type': 'multipart/form-data'
                                                        }
                                                        , responseType : "blob"
                                                    } ).then(
                                                        ( responseGet ) => {

                                                            let form = this.state.form;
                                                            let result = this.state.result;

                                                            form[ 'formDoc' ][ 'file' ] = null;
                                                            result[ 'thumbnails' ].push( {
                                                                documentId : responsePost.data.documentId
                                                                , links : [
                                                                    {
                                                                        rel : ''
                                                                        , href : window.URL.createObjectURL( new Blob( [ responseGet.data ] , { type : 'application/pdf' } ) )
                                                                        , method : ''
                                                                    }
                                                                ]
                                                            } );

                                                            this.setState(
                                                                {
                                                                    isDisableUpload     : false
                                                                    , form              : form
                                                                    , result            : result
                                                                } , () => {
                                                                    this.refs.cFormDocument.resetForm();
                                                                }
                                                            );

                                                        }
                                                    );

                                                }
                                            ).catch(
                                                ( error ) => {
                                                    console.log( error );

                                                    let form = this.state.form;
                                                    form[ 'formDoc' ][ 'file' ] = null;
                                                    this.setState( {
                                                        isDisableUpload : false
                                                        , form : form
                                                    } , () => {
                                                        this.refs.cFormDocument.resetForm();
                                                    } );
                                                }
                                            );

                                        }
                                    );

                                }
                            ).catch (
                                ( error ) => {
                                    this.requestError( error );
                                }
                            ) ;

                    }
                );


            }
            onClickCancel() {
                console.log( 'ComponentDocUploadUpload : onClickCancel : *** ' );

                this.requestReset();
            }














            /*
                ################################################################################################################################################
                ################################################################################################################################################
                        request
                ################################################################################################################################################
                ################################################################################################################################################
            */

            requestServicePackage() {
                console.log( 'ComponentDocUploadUpload : requestServicePackage : *** ' );


                this.setState(
                    { isLoading : true }
                    , () => {

                        HelperVerifyAuthorize.verify()
                            .then(
                                () => {

                                    Promise.all( [
                                        ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                                        , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                                    ] ).then(
                                        ( value ) => {

                                            this.dispatch(
                                                ReduxActionCompanyOnboardUpload.rServicePackage(
                                                    { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] }
                                                    , { }
                                                )
                                            )

                                        }
                                    );

                                }
                            ).catch (
                                ( error ) => {
                                    this.requestError( error );
                                }
                            ) ;

                    }
                )

            }
            requestServicePackageDone( result ) {
                console.log( 'ComponentDocUploadUpload : requestServicePackageDone : *** : result => ' , result );


                let data = this.state.data;
                data[ 'servicePackage' ] = result;
                this.setState( {
                    isLoading   : false
                    , data      : data
                } );

                this.dispatch( ReduxActionCompanyOnboardUpload.rRequestEnd( ) );
            }

            requestReset() {
                console.log( 'ComponentDocUploadUpload : requestReset : *** ' );

                let form = this.state.form;
                form[ 'formDoc' ][ 'thumbnailDocumentId' ]      = '';
                form[ 'formDoc' ][ 'thumbnailUploadDate' ]      = '';
                form[ 'formDoc' ][ 'thumbnailUploadBranch' ]    = '';
                form[ 'formDoc' ][ 'thumbnailUploadBy' ]        = '';
                form[ 'formDoc' ][ 'thumbnailUrl' ]             = '';

                form[ 'formDoc' ][ 'file' ]                     = null;
                form[ 'formDoc' ][ 'fileCompletePercentage' ]   = 0;

                form[ 'form1' ][ 'cifNumber' ]                  = '';
                form[ 'form1' ][ 'companyName' ]                = '';
                form[ 'form1' ][ 'servicePackage' ]             = '';


                let result = this.state.result;
                result[ 'thumbnails' ]                          = [];
                result[ 'acknowledgement' ]                     = {};

                this.setState( {
                    step  : 0
                    , form    : form
                    , result : result
                } );

            }


            requestDocumentInfo( documentId ) {
                console.log( 'ComponentDocUploadUpload : requestDocumentInfo : *** ' );

                this.setState(
                    { isLoading : true }
                    , () => {

                        HelperVerifyAuthorize.verify()
                            .then(
                                () => {

                                    Promise.all( [
                                        ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                                        , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                                    ] ).then(
                                        ( value ) => {

                                            this.dispatch(
                                                ReduxActionCompanyOnboardUpload.rDocument(
                                                    { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] }
                                                    , {
                                                        documentId : documentId
                                                    }
                                                )
                                            )

                                        }
                                    )

                                }
                            ).catch (
                                ( error ) => {
                                    this.requestError( error );
                                }
                            ) ;
                    }
                );

            }
            requestDocumentInfoDone( result ) {
                console.log( 'ComponentDocUploadUpload : requestDocumentInfoDone : *** : result => ' , result );

                let resultObj = this.state.result;
                resultObj[ 'thumbnails' ].push( result );
                this.setState( { result : resultObj } );

                this.dispatch( ReduxActionCompanyOnboardUpload.rRequestEnd( ) );

            }

            requestSubmitStep1() {
                console.log( 'ComponentDocUploadUpload : requestSubmitStep1 : *** ' );


                FormValidateUtil.validate(
                    this.state.form.form1
                    , {
                        cifNumber           : { required : true }
                        , servicePackage    : { required : true }
                    }
                ).then(
                    ( value ) => {

                        this.setState(
                            { isLoading : true , formValidation : {} }
                            , () => {

                                HelperVerifyAuthorize.verify()
                                    .then(
                                        () => {

                                            Promise.all( [
                                                ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                                                , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                                            ] ).then(
                                                ( value ) => {

                                                    let docs = [];
                                                    for ( let i = 0 ; i < this.state.result.thumbnails.length; i ++ ) {
                                                        docs.push( { documentId : this.state.result.thumbnails[ i ].documentId } );
                                                    }
                                                    this.dispatch(
                                                        ReduxActionCompanyOnboardUpload.rSubmit(
                                                            { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] }
                                                            , {
                                                                cifNumber   : this.state.form.form1.cifNumber
                                                                , spCode    : this.state.form.form1.servicePackage
                                                                , documents : docs
                                                            }
                                                        )
                                                    )

                                                }
                                            )

                                        }
                                    ).catch (
                                        ( error ) => {
                                            this.requestError( error );
                                        }
                                    ) ;
                            }
                        );

                    }
                ).catch(
                    ( error ) => {
                        this.setState( { formValidation : error } );
                    }
                );

            }
            requestSubmitStep1Done( result ) {
                console.log( 'ComponentDocUploadUpload : requestSubmitStep1Done : *** : result => ' , result );



                if ( ObjectUtil.hasProperty( result , 'cifNumber' ) && ObjectUtil.hasProperty( result , 'bussName' ) ) {

                    if ( result[ 'cifNumber' ] == this.state.form.form1.cifNumber ) {

                        let form = this.state.form;
                        form[ 'form1' ][ 'companyName' ] = result[ 'bussName' ];
                        this.setState( {
                            step            : this.state.step + 1
                            , isLoading     : false
                            , form          : form
                        } , () => {
                            console.log( this.state.form );
                        } );

                    } else {
                        this.requestError( new ExceptionGeneral( 'Cant Found Company Info ' ) );
                    }

                } else {
                    this.requestError( new ExceptionGeneral( 'Cant Found Company Info , please try again. ' ) );
                }

                this.dispatch( ReduxActionCompanyOnboardUpload.rRequestEnd( ) );

            }



            requestSubmitStep2() {
                console.log( 'ComponentDocUploadUpload : requestSubmitStep2 : *** ' );

                this.setState(
                    { isLoading : true }
                    , () => {

                        HelperVerifyAuthorize.verify()
                            .then(
                                () => {

                                    Promise.all( [
                                        ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                                        , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                                    ] ).then(
                                        ( value ) => {

                                            let docs = [];
                                            for ( let i = 0 ; i < this.state.result.thumbnails.length; i ++ ) {
                                                docs.push( { documentId : this.state.result.thumbnails[ i ].documentId } );
                                            }
                                            this.dispatch(
                                                ReduxActionCompanyOnboardUpload.rPreview(
                                                    { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] }
                                                    , {
                                                        cifNumber   : this.state.form.form1.cifNumber
                                                        , spCode    : this.state.form.form1.servicePackage
                                                        , documents : docs
                                                    }
                                                )
                                            )

                                        }
                                    )

                                }
                            ).catch (
                                ( error ) => {
                                    this.requestError( error );
                                }
                            ) ;
                    }
                );

            }
            requestSubmitStep2Done( result ) {
                console.log( 'ComponentDocUploadUpload : requestSubmitStep2Done : *** : result => ' , result );

                let resultObj = this.state.result;
                resultObj[ 'acknowledgement' ] = result;
                this.setState( {
                    step            : this.state.step + 1
                    , isLoading     : false
                    , result        : resultObj
                } );
                this.dispatch( ReduxActionCompanyOnboardUpload.rRequestEnd( ) );

            }

            requestSubmitStep3() {
                console.log( 'ComponentDocUploadUpload : requestSubmitStep3 : *** ' );

                this.setState(
                    { isLoading : true }
                    , () => {

                        HelperVerifyAuthorize.verify()
                            .then(
                                () => {

                                    Promise.all( [
                                        ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                                        , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                                    ] ).then(
                                        ( value ) => {

                                            this.dispatch(
                                                ReduxActionCompanyOnboardUpload.rAcknowlegdement(
                                                    { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] }
                                                    , {
                                                        cifNumber : ''
                                                        , spCode : ''
                                                        , documents : []
                                                    }
                                                )
                                            )

                                        }
                                    )

                                }
                            ).catch (
                                ( error ) => {
                                    this.requestError( error );
                                }
                            ) ;
                    }
                );

            }
            requestSubmitStep3Done( result ) {
                console.log( 'ComponentDocUploadUpload : requestSubmitStep3Done : *** : result => ' , result );

                this.setState( {
                    step            : this.state.step + 1
                    , isLoading     : false
                } );
                this.dispatch( ReduxActionCompanyOnboardUpload.rRequestEnd( ) );

            }


            requestDownloadPDF() {
                console.log( 'ComponentDocUploadUpload : requestDownloadPDF : *** ' );

                this.setState(
                    { isLoading : true }
                    , () => {

                        HelperVerifyAuthorize.verify()
                            .then(
                                () => {

                                    Promise.all( [
                                        ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                                        , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                                    ] ).then(
                                        ( value ) => {

                                            this.dispatch(
                                                ReduxActionCompanyOnboardUpload.rDownloadPDF(
                                                    { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] }
                                                    , {
                                                        cifNumber : ''
                                                        , spCode : ''
                                                        , documents : []
                                                    }
                                                )
                                            )

                                        }
                                    )

                                }
                            ).catch (
                                ( error ) => {
                                    this.requestError( error );
                                }
                            ) ;
                    }
                );

            }
            requestDownloadPDFDone( result ) {
                console.log( 'ComponentDocUploadUpload : requestDownloadPDFDone : *** : result => ' , result );

                this.setState( { isLoading : false } );
                this.dispatch( ReduxActionCompanyOnboardUpload.rRequestEnd( ) );

            }

            requestDownloadPrint() {
                console.log( 'ComponentDocUploadUpload : requestDownloadPrint : *** ' );

                this.setState(
                    { isLoading : true }
                    , () => {

                        HelperVerifyAuthorize.verify()
                            .then(
                                () => {

                                    Promise.all( [
                                        ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                                        , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                                    ] ).then(
                                        ( value ) => {

                                            this.dispatch(
                                                ReduxActionCompanyOnboardUpload.rDownloadPrint(
                                                    { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] }
                                                    , {
                                                        cifNumber : ''
                                                        , spCode : ''
                                                        , documents : []
                                                    }
                                                )
                                            )

                                        }
                                    )

                                }
                            ).catch (
                                ( error ) => {
                                    this.requestError( error );
                                }
                            ) ;
                    }
                );

            }
            requestDownloadPrintDone( result ) {
                console.log( 'ComponentDocUploadUpload : requestDownloadPrintDone : *** : result => ' , result );

                this.setState( { isLoading : false } );
                this.dispatch( ReduxActionCompanyOnboardUpload.rRequestEnd( ) );

            }

            requestError( result ) {
                console.log( 'ComponentDocUploadUpload : requestError : *** : result => ' , result );

                this.setState( { isLoading : false }
                    , () => {

                        this.handlerException(
                            result
                            , () => {
                                this.setState(
                                    {
                                        isError             : true
                                        , errorMessage      : result.msg
                                    }
                                );
                            }
                        );

                    }
                );

                this.dispatch( ReduxActionCompanyOnboardUpload.rRequestEnd( ) );
            }















            /*
                ################################################################################################################################################
                ################################################################################################################################################
                        render
                ################################################################################################################################################
                ################################################################################################################################################
            */
            render() {

                return (
                        <div>

                            <div className={ ' companyOnboard-upload-detail-padding-20 ' } >Company Onboard - Onboarding</div>

                            <div
                                    className={ ' companyOnboard-upload-detail-margin-auto companyOnboard-upload-detail-paddingTop-20 companyOnboard-upload-detail-paddingBottom-20  companyOnboard-upload-detail-maxWidth-900 ' }>

                                <Stepper activeStep={ this.state.step }>

                                    <Step>
                                        <StepLabel> Upload Documents </StepLabel>
                                    </Step>

                                    <Step>
                                        <StepLabel> Submit Form </StepLabel>
                                    </Step>

                                    <Step>
                                        <StepLabel> Preview </StepLabel>
                                    </Step>

                                    <Step>
                                        <StepLabel> Acknowledgement </StepLabel>
                                    </Step>

                                </Stepper>

                            </div>


                            <div className={ ' companyOnboard-upload-detail-divider ' }></div>


                            <Grid fluid>
                                <Row className="show-grid">
                                    <Col md={ 6 } >
                                        <ComponentDocUploadDocument
                                                    ref={ 'cFormDocument' }

                                                    form={ this.state.form.formDoc }

                                                    result={ this.state.result }

                                                    isDisableUpload={ this.state.isDisableUpload }
                                                    allowToUpload={ !( this.state.step > 1 ) }


                                                    onClickReset={ () => {
                                                        let form = this.state.form;
                                                        form[ 'formDoc' ][ 'file' ] = null;
                                                        this.setState( { form : form } );
                                                    } }

                                                    onClickUpload={ ( ) => {
                                                        this.onClickUpload( );
                                                    } }

                                                    onSelectFile={ ( file ) => {
                                                        let form = this.state.form;
                                                        form[ 'formDoc' ][ 'file' ] = file;
                                                        this.setState( { form : form } );
                                                    } }

                                                    onClickThumbnail={ ( docId ) => {
                                                        let form = this.state.form;
                                                        form[ 'formDoc' ][ 'thumbnailDocumentId' ] = '';
                                                        form[ 'formDoc' ][ 'thumbnailUploadDate' ] = '';
                                                        form[ 'formDoc' ][ 'thumbnailUploadBranch' ] = '';
                                                        form[ 'formDoc' ][ 'thumbnailUploadBy' ] = '';
                                                        form[ 'formDoc' ][ 'thumbnailUrl' ] = '';

                                                        for ( let i = 0; i < this.state.result.thumbnails.length; i ++ ) {
                                                            if ( this.state.result.thumbnails[ i ][ 'documentId' ] == docId ) {
                                                                let link = '';
                                                                if ( this.state.result.thumbnails[ i ].links.length > 0 ) {
                                                                    link = this.state.result.thumbnails[ i ].links[ 0 ].href;
                                                                }

                                                                form[ 'formDoc' ][ 'thumbnailDocumentId' ]        = docId;
                                                                form[ 'formDoc' ][ 'thumbnailUploadDate' ]        = '-'
                                                                form[ 'formDoc' ][ 'thumbnailUploadBranch' ]      = '-';
                                                                form[ 'formDoc' ][ 'thumbnailUploadBy' ]          = '-';
                                                                form[ 'formDoc' ][ 'thumbnailUrl' ]               = link;
                                                            }
                                                        }
                                                        this.setState( { form : form } );
                                                    } }
                                        />
                                    </Col>

                                    <Col md={ 6 }>

                                        {
                                            ( () => {

                                                switch ( this.state.step ) {

                                                    case 0:
                                                        return (
                                                            <div className={ ' companyOnboard-upload-detail-textAlign-center companyOnboard-upload-detail-paddingTop-20 ' }>
                                                                Need Documents to Next Step
                                                            </div>
                                                        );
                                                        break;


                                                    case 1:
                                                        return (
                                                            <ComponentDocUploadStep1

                                                                        form={ this.state.form.form1 }
                                                                        formValidation={ this.state.formValidation }

                                                                        data={ this.state.data }

                                                                        onChangeForm={ ( key , value ) => {
                                                                            let form = this.state.form;
                                                                            form[ 'form1' ][ key ] = value;
                                                                            this.setState( { form : form } );
                                                                        } }
                                                                        onClickSubmit={ () => {
                                                                            this.requestSubmitStep1();
                                                                        } }
                                                                        onClickCancel={ () => {
                                                                            this.onClickCancel();
                                                                        } }
                                                            />
                                                        );
                                                        break;

                                                    case 2:
                                                        return (
                                                            <ComponentDocUploadStep2

                                                                        form={ this.state.form.form1 }

                                                                        data={ this.state.data.servicePackage }

                                                                        onClickBack={ () => {
                                                                            this.setState( { step : this.state.step - 1 } );
                                                                        } }
                                                                        onClickSubmit={ () => {
                                                                            this.requestSubmitStep2();
                                                                        } }
                                                                        onClickCancel={ () => {
                                                                            this.onClickCancel();
                                                                        } }
                                                            />
                                                        );
                                                        break;

                                                    case 3:
                                                        return (
                                                            <ComponentDocUploadStep3

                                                                        form={ this.state.form.form1 }

                                                                        data={ this.state.data.servicePackage }

                                                                        result={ this.state.result }

                                                                        onClickPDF={ () => {
                                                                            this.requestDownloadPDF();
                                                                        } }
                                                                        onClickPrint={ () => {
                                                                            this.requestDownloadPrint();
                                                                        } }
                                                                        onClickOK={ () => {
                                                                            this.requestReset();
                                                                        } }
                                                            />
                                                        );
                                                        break;

                                                    default:
                                                        return null;

                                                }

                                            } )()
                                        }

                                    </Col>
                                </Row>
                            </Grid>




                            <ComponentCustomLoading
                                            visible={ this.state.isLoading }
                            />

                            <ComponentCustomAlert
                                            visible={ this.state.isError }
                                            alertType={ 'error' }
                                            msg={ this.state.errorMessage }

                                            onClose={ () => {
                                                this.setState( { isError : false , errorMessage : ''  } );
                                            } }
                            />

                        </div>
                );
            };
        }

    )



