import React                                        from 'react';
import PropTypes                                    from 'prop-types';




import {
    Grid , Row , Col
}                                                   from 'react-bootstrap';
import FlatButton                                   from 'material-ui/FlatButton';
import TextField                                    from 'material-ui/TextField';
import {
    RadioButton
    , RadioButtonGroup
}                                                   from 'material-ui/RadioButton';
import RaisedButton                                 from 'material-ui/RaisedButton';
import LinearProgress                               from 'material-ui/LinearProgress';






/* ################################################### */
/*          constant         */
/* ################################################### */
import { AppConstant }                              from '../../../../../_constant/constant.app';
import { ServiceEnumHTTP }                          from '../../../../../_service/helper/service.enum.http';








/* ################################################### */
/*          abstract         */
/* ################################################### */
import ComponentAbstract                            from '../../../../_abstract/c.abstract';






import './s.docUpload.document.css';





export default class ComponentDocUploadDocument extends ComponentAbstract {


    static propTypes = {
        form                    : PropTypes.object
        , result                : PropTypes.object

        , isDisableUpload       : PropTypes.bool
        , allowToUpload         : PropTypes.bool

        , onClickReset          : PropTypes.func
        , onClickThumbnail      : PropTypes.func
        , onSelectFile          : PropTypes.func
        , onClickUpload         : PropTypes.func
    }


    static defaultProps = {
        allowToUpload               : false
        , isDisableUpload           : false

        , onClickReset              : () => {}
        , onClickThumbnail          : () => {}
        , onSelectFile              : () => {}
        , onClickUpload             : () => {}
    }





    constructor( props ) {
        super( props )

        this.state = {
            containerWidth : 0
        }

    }




    resetForm() {
        console.log( 'ComponentDocUploadDocument : resetForm : *** ' );

        this.refs.inputForm.reset();
    }








    bytesToSize( bytes ) {
        var sizes = [ 'Bytes' , 'KB' , 'MB' , 'GB' , 'TB' ];
        if ( bytes == 0 ) return '0 Byte';
        var i = parseInt( Math.floor( Math.log( bytes ) / Math.log( 1024 ) ) );
        return Math.round( bytes / Math.pow( 1024 , i ) , 2 ) + ' ' + sizes[ i ];
    }








    /*
        ################################################################################################################################################
        ################################################################################################################################################
                render
        ################################################################################################################################################
        ################################################################################################################################################
    */
    render() {

        return (
            <div
                    className={ ' companyOnboard-upload-document-container ' }
                    ref={ ( refElement ) => {
                        if ( refElement ) {
                            if ( this.state.containerWidth == 0 && refElement.clientWidth != 0 ) {
                                this.setState( { containerWidth : refElement.clientWidth } );
                            }
                        }
                    } } >
                <div>
                    <div
                            className={ ' companyOnboard-upload-document-thumbnail-container ' }
                            style={ {
                                width : this.state.containerWidth + 'px'
                            } }>

                        <div
                                className={ ' companyOnboard-upload-document-thumbnail-container-scroll ' }
                                style={ { width : ( ( 110 * this.props.result.thumbnails.length ) + 20 ) + 'px' } }>
                            {
                                ( this.props.result.thumbnails ).map( ( value , index ) => {

                                    let link = '';
                                    if ( value.links.length > 0 ) {
                                        link = value.links[ 0 ].href;
                                    }

                                    return (
                                        <div
                                                key={ '_thumbnail_' + index }
                                                className={ ' companyOnboard-upload-document-thumbnail ' } >

                                            <div style={ { position : 'relative' , overflow : 'hidden' } }>
                                                <object
                                                        data={ link }
                                                        type="application/pdf"
                                                        className={ ' companyOnboard-upload-document-image ' }>

                                                </object>
                                                <div
                                                        style={ {
                                                            position : 'absolute' , top : '0px' , left : '0px' , width : '90px' , height : '110px'
                                                            , backgroundColor : 'rgba( 4 , 4 , 4 , 0.1 )'
                                                        } }
                                                        onClick={ () => {
                                                            this.props.onClickThumbnail( value.documentId )
                                                        } }> </div>
                                            </div>

                                        </div>
                                    );
                                } )
                            }
                        </div>
                    </div>

                </div>


                <div className={ ' companyOnboard-upload-document-divider ' }></div>



                <div className={ ' companyOnboard-upload-document-padding-10 ' } >
                    <Grid fluid>
                        <Row className="show-grid">
                            <Col md={ 12 } >
                                Uploaded Date : { this.props.form.thumbnailUploadDate }
                            </Col>
                        </Row>
                    </Grid>

                    <Grid fluid>
                        <Row className="show-grid">
                            <Col md={ 6 } >
                                Uploaded Branch : { this.props.form.thumbnailUploadBranch }
                            </Col>
                            <Col md={ 6 } >
                                uploaded By : { this.props.form.thumbnailUploadBy }
                            </Col>
                        </Row>
                    </Grid>
                </div>


                <div className={ ' companyOnboard-upload-document-padding-10 companyOnboard-upload-document-height-70p companyOnboard-upload-document-height-max-700 companyOnboard-upload-document-border ' }>

                    {
                        this.props.form.thumbnailDocumentId == ''
                        ?
                            ( null )
                        :
                            ( () => {

                                return (
                                    <embed
                                            src={ this.props.form.thumbnailUrl }
                                            className={ ' companyOnboard-upload-document-image ' }
                                            style={ { width : '100%' , height : '100%' } }
                                    />
                                );

                            } )( )
                    }

                </div>

                {
                    this.props.allowToUpload
                    ?
                        (
                            <div className={ ' companyOnboard-upload-document-divider ' }></div>
                        )
                    :
                        ( null )
                }

                {
                    this.props.allowToUpload
                    ?
                        (
                            <div className={ ' companyOnboard-upload-document-padding-10 ' } >


                                <Grid>
                                    <Row className="show-grid">
                                        <Col md={ 12 } >
                                            <RaisedButton
                                                label={ 'Cancel' }
                                                className={ ' companyOnboard-upload-document-margin-20 ' }
                                                disabled={ this.props.isDisableUpload }
                                                onClick={ () => {
                                                    this.resetForm();
                                                    this.props.onClickReset();
                                                } } />

                                            <RaisedButton
                                                label={ 'Upload' }
                                                className={ ' companyOnboard-upload-document-margin-20 ' }
                                                disabled={ this.props.isDisableUpload ? true : this.props.form.file == null }
                                                onClick={ () => {
                                                    this.props.onClickUpload();
                                                } } />

                                            <RaisedButton
                                                label={ this.props.form.file == null ? 'Browser' : ( this.props.form.file.name +  ' (' + this.bytesToSize( this.props.form.file.size ) + ')' ) }
                                                primary={ this.props.form.file != null }
                                                disabled={ this.props.isDisableUpload }
                                                className={ ' companyOnboard-upload-document-margin-20 ' }
                                                onClick={ () => {
                                                    this.refs.inputUpload.click();
                                                } } />

                                        </Col>
                                    </Row>
                                </Grid>


                                <form
                                        ref={ 'inputForm' }>
                                    <input
                                        ref={ 'inputUpload' }

                                        type='file'
                                        multiple={ false }
                                        style={ { display : 'none' } }

                                        onChange={ ( event ) => {
                                            this.props.onSelectFile( event.target.files.length == 0 ? null : event.target.files[ 0 ] );
                                        } }
                                    />
                                </form>

                                {
                                    this.props.isDisableUpload
                                    ?
                                        ( <LinearProgress mode="determinate" value={ this.props.form.fileCompletePercentage } /> )
                                    :
                                        ( null )
                                }

                            </div>
                        )
                    :
                        ( null )

                }

            </div>
        );
    };
}



