import React                                        from 'react';
import PropTypes                                    from 'prop-types';





import {
    Grid , Row , Col
}                                                   from 'react-bootstrap';
import FlatButton                                   from 'material-ui/FlatButton';
import TextField                                    from 'material-ui/TextField';
import {
    RadioButton
    , RadioButtonGroup
}                                                   from 'material-ui/RadioButton';
import RaisedButton                                 from 'material-ui/RaisedButton';








/* ################################################### */
/*          abstract         */
/* ################################################### */
import ComponentAbstract                            from '../../../../_abstract/c.abstract';






import './s.docUpload.step.2.css';





export default class ComponentDocUploadStep2 extends ComponentAbstract {


    static propTypes = {
        form                : PropTypes.object

        , data              : PropTypes.array

        , onClickBack       : PropTypes.func
        , onClickSubmit     : PropTypes.func
        , onClickCancel     : PropTypes.func
    }


    static defaultProps = {
        form      : {}

        , data          : []

        , onClickBack   : () => {}
        , onClickSubmit : () => {}
        , onClickCancel : () => {}
    }










    constructor( props ) {
        super( props )
    }









    /*
        ################################################################################################################################################
        ################################################################################################################################################
                render
        ################################################################################################################################################
        ################################################################################################################################################
    */
    render() {

        return (
                <div>

                    <h5
                            style={ {
                                marginBottom    : 0
                                , marginTop     : 15
                                , marginBottom  : 30
                            } }>
                        Preview
                    </h5>

                    <hr />


                    <div className={ ' companyOnboard-upload-step2-padding ' }>

                        <Grid>
                            <Row className="show-grid">
                                <Col md={ 4 } >
                                    CIF Number
                                </Col>

                                <Col md={ 6 }>
                                    { this.props.form[ 'cifNumber' ] }
                                </Col>
                            </Row>
                        </Grid>

                    </div>


                    <div className={ ' companyOnboard-upload-step2-padding ' }>

                        <Grid>
                            <Row className="show-grid">
                                <Col md={ 4 } >
                                    Company Name
                                </Col>

                                <Col md={ 6 }>
                                    { this.props.form[ 'companyName' ] }
                                </Col>
                            </Row>
                        </Grid>

                    </div>


                    <div className={ ' companyOnboard-upload-step2-padding ' }>

                        <Grid>
                            <Row className="show-grid">
                                <Col md={ 4 } >
                                    Select Service Package
                                </Col>

                                <Col md={ 6 }>
                                    {
                                        ( () => {
                                            let label = '';
                                            for ( let i = 0; i < this.props.data.length; i ++ ) {
                                                let ObjJ = this.props.data[ i ];
                                                if ( ObjJ[ 'spCode' ] == this.props.form[ 'servicePackage' ] ) {
                                                    label = ObjJ[ 'spName' ];
                                                }
                                            }
                                            return label;
                                        } )()
                                    }
                                </Col>
                            </Row>
                        </Grid>

                    </div>


                    <div className={ ' companyOnboard-upload-step2-textAlign-center ' }>

                        <RaisedButton
                            label={ 'Back' }
                            className={ ' companyOnboard-upload-step2-margin-20 ' }
                            onClick={ () => {
                                this.props.onClickBack();
                            } } />

                        <RaisedButton
                            label={ 'Cancel' }
                            className={ ' companyOnboard-upload-step2-margin-20 ' }
                            onClick={ () => {
                                this.props.onClickCancel();
                            } } />

                        <RaisedButton
                            label={ 'Submit' }
                            className={ ' companyOnboard-upload-step2-margin-20 ' }
                            onClick={ () => {
                                this.props.onClickSubmit();
                            } } />

                    </div>



                </div>
        );
    };
}



